@font-face {
  font-family: 'main'; /*a name to be used later*/
  src: url('./Bebas-Regular.ttf'); /*URL to font*/
}

* {
  margin: 0px;
  text-decoration: none;
  color: white;
  font-family: main;
}

.App {
  position: fixed;
  width: 100%;
  height: 100%;
  text-align: center;
  /*background-color:rgb(40, 44, 52) !important; 
  background-color: rgba(40, 44, 52, 0.5);*/
}

.bgimg {
  position: fixed;
  width: 100%;
  height: 92%;
  top: 8%;
  left: 0;
  background-image: url("./bgimg.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

a:visited {
  color: white;
}
a {
  text-decoration: none;
  color: white;
}

.info {
  position: fixed;
  left: 50%;
  top: 40%;
  width: 100%;
  color: white;
  font-size: 110px;
  /*display: inline-block;*/
}
.info p {
  position: absolute;
  transform: translate(-50%, -50%);
}

.meetLink {
  position: fixed;
  left: 50%;
  transform: translate(-50%, -50%);
  top: 50%;
  font-size: 40px;
  padding: 20px;
  background-color: #17181b;
  border-radius: 10px;
  box-shadow: 5px black;
}
.meetLink:hover {
  padding: 21px;
  font-size: 51px;
}

.navBar {
  position: fixed;
  width: 100%;
  height: 8%;
  background-color: #17181b;
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  list-style-type: none;
  text-align: center;
  display: flex;
  align-items: stretch; /* Default */
  justify-content: space-between;
  width: 65%;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  
}

li {
  display: block;
  flex: 0 1 auto; /* Default */
  list-style-type: none;
}

.logo {
  position: absolute;
  height: 70%;
  width: 10%;
  left: 6%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.logo img {
  width: 100%;
  height: 100%;
}

.sixth {
  position: fixed;
  width: 35%;
  height: 5%;
  bottom: 2%;
  left: 50%;
  transform: translateX(-50%);
}